<template>
  <div class="main" v-loading="loading">
    <div class="header">
      <span class="logo"></span>
      位讯旅游
    </div>
    <div class="nav-title">
      <span class="left-icon">派团</span>
    </div>
    <el-form
      :model="groupadds" ref="groupadds"
      class="new-from demo-ruleForm"
      :validate-on-rule-change="false">
      <el-form-item label="出团通知："
                    prop="required"
                    :rules="[{ required: true, message: '出团通知不能为空'}]">
        <div class="import member-btn">
          <input type="file" @change="noticeImport($event)"/>
          <span>导入</span>
          <span class="g-tips import-tip">可导入excel、word、pdf文件或图片</span>
          <div class="files-container">
            <div class="file-tags" v-for="(val, index) in tuanFileImg">
              <el-button
                type="success"
                size="small"
                class="success"
                @click="dialogChange('tuanImgFile', val, index)">
                {{val.name ? val.name : '附件'+(index+1)}}&nbsp;&nbsp;&nbsp;
                <i class="el-icon-circle-close-outline"></i>
              </el-button>
              <el-dialog
                title="提示"
                :visible.sync="dialogVisible"
                class="el-dialog-width">
                <span>确定删除该附件？</span>
                <span slot="footer" class="dialog-footer">
                <el-button type="success" @click="deleFile">确 定</el-button>
                <el-button @click="dialogVisible = false">取 消</el-button>
              </span>
              </el-dialog>
            </div>
          </div>
          <div class="files-container">
            <div class="file-tags" v-for="(val, index) in showTuanFiles">
              <el-button
                type="success"
                size="small"
                class="success"
                @click="dialogChange('tuanfiles', val, index)">
                {{val.name ? val.name : '附件'+(index+1)}}&nbsp;&nbsp;&nbsp;
                <i class="el-icon-circle-close-outline"></i>
              </el-button>
              <el-dialog
                title="提示"
                :visible.sync="dialogVisible"
                class="el-dialog-width">
                <span>确定删除该附件？</span>
                <span slot="footer" class="dialog-footer">
                <el-button type="success" @click="deleFile($event, val, index)">确 定</el-button>
                <el-button @click="dialogVisible = false">取 消</el-button>
              </span>
              </el-dialog>
            </div>
          </div>
        </div>
      </el-form-item>
      <el-form-item label="名单附件："
                    prop="required"
                    :rules="[{ required: true, message: '名单附件不能为空'}]">
        <div class="import member-btn">
          <input type="file" @change="nameImport($event)"/>
          <span>导入</span>
          <span class="g-tips import-tip">可导入excel、word、pdf文件或图片</span>
          <div class="files-container">
            <div class="file-tags" v-for="(val, index) in rosterFileImg">
              <el-button
                type="success"
                size="small"
                class="success"
                @click="dialogChange('rosterImgFile', val, index)">
                {{val.name ? val.name : '附件'+(index+1)}}&nbsp;&nbsp;&nbsp;
                <i class="el-icon-circle-close-outline"></i>
              </el-button>
              <el-dialog
                title="提示"
                :visible.sync="dialogVisible"
                class="el-dialog-width">
                <span>确定删除该附件？</span>
                <span slot="footer" class="dialog-footer">
                <el-button type="success" @click="deleFile">确 定</el-button>
                <el-button @click="dialogVisible = false">取 消</el-button>
              </span>
              </el-dialog>
            </div>
          </div>
          <div class="files-container">
            <div class="file-tags" v-for="(val, index) in showRosterFiles">
              <el-button
                type="success"
                size="small"
                class="success"
                @click="dialogChange('rosterFiles', val, index)">
                {{val.name ? val.name : '附件'+(index+1)}}&nbsp;&nbsp;&nbsp;
                <i class="el-icon-circle-close-outline"></i>
              </el-button>
              <el-dialog
                title="提示"
                :visible.sync="dialogVisible"
                class="el-dialog-width">
                <span>确定删除该附件？</span>
                <span slot="footer" class="dialog-footer">
                <el-button type="success" @click="deleFile($event, val, index)">确 定</el-button>
                <el-button @click="dialogVisible = false">取 消</el-button>
              </span>
              </el-dialog>
            </div>
          </div>
        </div>
      </el-form-item>
      <el-form-item label="导游电话："
                    prop="guideMobile"
                    :rules="[
        {pattern: /^(13[0-9]|14[0-9]|15[0-9]|16[6]|18[0-9]|19[6,9]|17[0-9])\d{8}$/i, message: '手机号格式错误', trigger: 'blur'}
      ]">

        <!--{ required: true, message: '号码不能为空', trigger: 'blur' },-->
        <!--{ type: 'number', message: '号码必须为数字值', trigger: 'blur' },-->
        <el-input style="width:150px" class="phone-input" size="small "
                  v-model.number="groupadds.guideMobile"></el-input>
      </el-form-item>
      <el-form-item label="导游姓名：" prop="guideName">
        <el-input style="width:150px" class="phone-input" size="small " v-model="groupadds.guideName"></el-input>
      </el-form-item>
      <el-form-item label="旅行社名称：" prop="lxName">
        <el-input style="width:150px" class="phone-input" size="small " v-model="groupadds.lxName"></el-input>
      </el-form-item>
      <el-form-item>
        <div class="form-btn">
          <div class="btn">
            <el-button type="primary" @click="submitForm()">提交</el-button>
            <el-button @click="resetForm">重置</el-button>
          </div>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        loading: false,
        ruleForm: {
          required: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        },
        Http: '',
        groupadds: {
          taskName: '',
          guideName: '',
          guideMobile: '',
          lxName: '', //旅行社名称
          demo: '',
          tuanFile: '',
          rosterFile: ''
        },
        showTuanUpload: true,
        showRosterFileUpload: true,
        tuanFileImg: [],
        showTuanFiles: [],
        uploadTuanFile: [],
        showRosterFiles: [],
        rosterFileImg: [],
        uploadRosterFile: [],
        fileNames: [],
        nameIndex: -1,
        searchPhoneHttp: '',
        uploadRosterFileName: [],
        dialogVisible: false,
        delFile: {
          index: '',
          f_type: '',
          val: ''
        }
      };
    },
    methods: {
      noticeImport(e) {
        let self = this;
        let file = e.target.files[0];
        let nameStr = '';
        if (file.name) {
          nameStr = file.name.split(".")[0];
          self.uploadRosterFileName.push(nameStr);
        }
        let reader = new FileReader();
        if (!/image\/\w+/.test(file.type)) {
          self.showTuanFiles.push(file);
        } else {
          reader.onload = function (e) {
            let result = e.target.result;
            if (self.tuanFileImg.indexOf(result) === -1) {
              self.tuanFileImg.push(result);
            } else {
              self.$message.error('图片已存在');
            }
          };
          reader.readAsDataURL(file);
        }
        self.uploadTuanFile.push(file);

        self.groupadds.taskName = self.uploadRosterFileName[self.uploadRosterFileName.length - 1] || '';
      },
      nameImport(e) {
        let self = this;
        let file = e.target.files[0];
        let reader = new FileReader();
        if (!/image\/\w+/.test(file.type)) {
          console.log(file, self.showRosterFiles)
          self.showRosterFiles.push(file);
        } else {
          reader.onload = function (e) {
            let result = e.target.result;
            if (self.rosterFileImg.indexOf(result) === -1) {
              self.rosterFileImg.push(result);
            } else {
              self.$message.error('图片已存在');
            }
          };
          reader.readAsDataURL(file);
        }
        self.uploadRosterFile.push(file);
      },
      dialogChange(e, val, index) {
        this.dialogVisible = true
        this.delFile.f_type = e
        this.delFile.value = val
        this.delFile.index = index
      },
      deleFile() {
        let f_type = this.delFile.f_type;
        let self = this;
        if (typeof this.delFile.value === 'string' && f_type === 'tuanImgFile') {
          self.tuanFileImg.splice(this.delFile.index, 1);
        }
        if (typeof this.delFile.value === 'object' && f_type === 'tuanfiles') {
          self.showTuanFiles.splice(this.delFile.index, 1);
          self.uploadTuanFile.splice(this.delFile.index - 1, 1);
        }
        // 本地文件
        if (typeof this.delFile.value === 'string' && f_type === 'rosterImgFile') {
          self.rosterFileImg.splice(this.delFile.index, 1);
        }
        if (typeof this.delFile.value === 'object' && f_type === 'rosterFiles') {
          self.showRosterFiles.splice(this.delFile.index, 1);
          self.uploadRosterFile.splice(this.delFile.index - 1, 1);
        }
        this.dialogVisible = false
      },
      deleFiles(e, val, index) {
        let f_type = e.target.dataset.type;
        let self = this;
        layer.confirm('确定删除该附件？', {
          btn: ['确定', '取消'] //按钮
        }, function () {
          // 本地图片
          if (typeof val === 'string' && f_type === 'tuanImgFile') {
            self.tuanFileImg.splice(index, 1);
          }
          if (typeof val === 'object' && f_type === 'tuanfiles') {
            self.showTuanFiles.splice(index, 1);
            self.uploadTuanFile.splice(index - 1, 1);
          }
          // 本地文件
          if (typeof val === 'string' && f_type === 'rosterImgFile') {
            self.rosterFileImg.splice(index, 1);
          }
          if (typeof val === 'object' && f_type === 'rosterFiles') {
            self.showRosterFiles.splice(index, 1);
            self.uploadRosterFile.splice(index - 1, 1);
          }
          $('.layui-layer-shade, .layui-layer').hide();
        }, function () {
          // 取消
        })
      },
      submitForm() {
        var url = window.location.search
        if (url === '') {
          url = '?sid=qc893ja8gp914ikpl6v9rqifv6rq14tc'
        }
        this.loading = true
        let self = this;
        // let isMobile = /^(13|14|15|17|18)\d{9}$/;
        let isMobile = /^(13[0-9]|14[0-9]|15[0-9]|16[6]|18[0-9]|19[6,9]|17[0-9])\d{8}$/i;
        if (self.uploadTuanFile.length === 0) {
          this.$message.error('请上传出团通知书附件');
          this.loading = false
          return false
        }
        if (self.uploadRosterFile.length === 0) {
          this.$message.error('请上传名单附件');
          this.loading = false
          return false
        }
        if (!isMobile.test(self.groupadds.guideMobile)) {
          this.$message.error('请填写正确手机号');
          this.loading = false
          return false
        }
        let formData = new FormData(); // 基础信息表单数据
        formData.append('taskName', self.groupadds.taskName);
        formData.append('guideName', self.groupadds.guideName);
        formData.append('guideMobile', self.groupadds.guideMobile);
        formData.append('lxName', self.groupadds.lxName);
        formData.append('demo', self.groupadds.demo);

        if (self.uploadTuanFile.length) {
          for (let item of self.uploadTuanFile) {
            formData.append('tuanFile[]', item);
          }
        } else {
          formData.append('tuanFile[]', self.uploadTuanFile);
        }
        if (self.uploadRosterFile.length) {
          for (let item of self.uploadRosterFile) {
            formData.append('rosterFile[]', item);
          }
        } else {
          formData.append('rosterFile[]', self.uploadRosterFile);
        }
        let config = {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        };
        self.$http.post('http://192.168.11.188:8001/wbPaituan/setWbPaituan' + url, formData, config).then(function (res) {
          this.loading = false
          let result = res.data.result || '';
          if (result.WbPaituan) {
            self.groupadds = {
              taskName: '',
              guideName: '',
              guideMobile: '',
              lxName: '', //旅行社名称
              demo: '',
              tuanFile: '',
              rosterFile: ''
            };
            self.uploadRosterFile = [];
            self.uploadTuanFile = [];
            self.showTuanFiles = [];
            self.showRosterFiles = [];
            self.tuanFileImg = [];
            self.rosterFileImg = [];
            this.$message({
              message: '派团成功！',
              type: 'success'
            });
          }
          else {
            this.$message.error('派团失败！');
            this.loading = false
          }
        }, function (res) {
          this.$message.error('派团失败！');
          this.loading = false
        });
      },
      resetForm() {
        this.groupadds = {
          taskName: '',
          guideName: '',
          guideMobile: '',
          lxName: '', //旅行社名称
          demo: '',
          tuanFile: '',
          rosterFile: ''
        };
        this.uploadRosterFile = [];
        this.uploadTuanFile = [];
        this.showTuanFiles = [];
        this.showRosterFiles = [];
        this.tuanFileImg = [];
        this.rosterFileImg = [];
      }
    }
  }
</script>

<style lang="scss" scoped>

  @media screen and (max-width: 640px) {
    .main {
      .header, .nav-title {
        margin-bottom: 0.10rem;
      }
      .header {
        height: 0.35rem;
        line-height: 0.35rem;
        padding-left: 20px;
        font: normal bold 0.12rem/0.35rem arial, verdana;
        .logo {
          width: 0.23rem;
          height: 0.23rem;
          background: url(../assets/image/logo.png) no-repeat;
          background-size: 0.21rem;
        }
      }
      .nav-title {
        height: 0.35rem;
        line-height: 0.35rem;
        .left-icon {
          font-size: 0.14rem;
        }
      }
      .new-from {
        padding-bottom: 0.2rem;
        .import {
          .files-container {
            margin-left: 0.12rem;
            span {
              font-size: 0.14rem;
            }
          }
        }
      }
    }
  }

  @media screen and (min-width: 640px) {
    .main {
      .header, .nav-title {
        margin-bottom: 20px;
      }
      .header {
        height: 68px;
        padding-left: 20px;
        font: normal bold 22px/68px arial, verdana;
        .logo {
          width: 35px;
          height: 35px;
          background: url(../assets/image/logo.png) no-repeat 0 center;
          background-size: 35px;
        }
      }
      .nav-title {
        height: 68px;
        line-height: 68px;
        .left-icon {
          font-size: 24px;
        }
      }
      .new-from {
        .import {
          .import-tip {
            margin-left: 105px;
          }
          .files-container {
            span {
              font-size: 12px;
            }
          }
        }
      }
    }
  }

  .main {
    background-color: #f6f8f7;
    height: 100%;
    width: 100%;
    .header {
      display: inline-block;
      width: 100%;
      color: #fff;
      background: #0aba79;
      .logo {
        display: inline-block;
        padding-left: 15px;
        vertical-align: middle;
      }
    }
    .nav-title {
      width: 100%;
      padding-left: 20px;
      background: #fff;
      .left-icon {
        height: 22px;
        padding-left: 10px;
        border-left: 5px solid #0aba79;
      }
    }
    .new-from {
      padding: 10px 0 50px;
      background-color: #fff;
      .import {
        display: inline-block;
        position: relative;
        height: 34px;
        padding: 0 0 0 0;
        border: none;
        cursor: pointer;
        .files-container {
          span {
            height: 24px;
            padding: 0 8px;
            line-height: 22px;
          }
        }
        input {
          position: absolute;
          opacity: 0;
          width: 100%;
          height: 100%;
          cursor: pointer;
        }
        span {
          padding: 0 10px 0 22px;
          cursor: pointer;
          color: #09ba79;
          font-size: 14px;
          height: 34px;
          line-height: 34px;
        }
        .import-tip {
          margin-top: 6px;
          padding-top: 6px;
          color: #999;
          font-size: 12px;
        }
        span.dele-file-icon, span.dele-img-icon {
          position: absolute;
          background: rgba(0, 0, 0, 0.5);
          color: #fff;
          width: 18px;
          height: 18px;
          padding: 0;
          line-height: 18px;
          top: -8px;
          right: -8px;
          text-align: center;
          border-radius: 50%;
          /*visibility: hidden;*/
        }
        .file-imgs {
          position: relative;
          display: inline-block;
          width: 120px;
          height: 80px;
          border-radius: 6px;
          border: 1px solid #eee;
          margin-bottom: 20px;
          margin-right: 10px;
        }
        .file-imgs img {
          display: block;
          width: 100%;
          height: 100%;
        }
        .vue-datepicker {
          display: inline-block;
          background: #fff url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABoAAAAkCAIAAAAYWL1EAAAAGXRFW…DNJT/s4H4Eksi+pihmgTHw6f270WQ8atyocaPG0dc4gAADAALLNkNyjdPhAAAAAElFTkSuQmCC) no-repeat scroll right center;
          padding-right: 35px;
        }
        .file-tags {
          display: inline-block;
          position: relative;
          margin-right: 6px;
          color: #fff;
          line-height: 30px;
          a {
            color: #fff;
            text-decoration: none;
          }
        }
      }
    }
  }

</style>
<style lang="scss">
  .main {
    .new-from {
      .import, .form-btn {
        .files-container, .btn {
          .el-button--success,
          .el-button--success:active,
          .el-button--success:focus,
          .el-button--success:hover,
          .el-button--success.is-active,
          .el-button--success:active
          .el-button--primary:focus,
          .el-button--primary:hover,
          .el-button--primary {
            background-color: #09ba79;
          }
          .el-dialog__body {
            padding: 5px 25px 5px;
            span {
              color: #000;
            }
          }
        }
      }
      .phone-input {
        .el-input.is-active .el-input__inner, .el-input__inner:focus {
          border: 1px solid #09ba79;
        }
      }
      .el-form-item__label {
        width: 150px;
      }
      .el-input__inner {
        border: 1px solid #b8c0d4;
      }
    }
  }

  @media screen and (max-width: 640px) {
    .main {
      .new-from {
        padding-top: 0.1rem;
        .import {
          .el-dialog-width {
            width: 100%;
            .el-dialog {
              width: 70%;
              margin: 20% 13% 0;
              .el-dialog__header {
                padding: 0.08rem 0.1rem 0.08rem;
              }
              .el-dialog__headerbtn {
                top: 0.1rem;
              }
              .el-dialog__body {

              }
              .el-dialog__footer {
                padding: 0 0.1rem 0.15rem 0;
                .el-button {
                  padding: 0.05rem 0.12rem;
                }
              }
            }
          }
          .el-input--small .el-input__inner {
            height: 27px;
            line-height: 27px;
          }
        }
        .el-form-item__error {
          left: 0.9rem;
        }
        .el-form-item__label {
          width: 0.9rem;
          line-height: 0.2rem;
        }
        .el-form-item__content {
          line-height: 0.2rem;
          .form-btn {
            margin-left: 0.2rem;
            .el-button {
              padding: 0.07rem 0.15rem;
            }
          }
        }
        .el-button--small, .el-button--small.is-round {
          padding: 0.05rem 0.08rem;
        }
      }
    }
  }

  @media screen and (min-width: 640px) {
    .main {
      .new-from {
        .import {
          .el-dialog-width {
            width: 30%;
          }
        }
        .el-form-item__label {
          width: 150px;
        }
        .el-form-item__error {
          left: 150px;
        }
        .el-form-item__content {
          .form-btn {
            margin-left: 150px;
          }
        }
      }
    }
  }
</style>



