import Vue from 'vue'
import Router from 'vue-router'
import Guider from '../components/Guider'
// import 'lib-flexible/flexible.js'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/group',
      name: 'Guider',
      component: Guider
    }
  ]
})



